.i{
    display: flex;
    height: 100vh;
    padding-top: 70px;
}

.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}

.i-right{
    flex: 1;
    position: relative;
}

.i-wrapperleft{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro{
    font-size: 30px;
    font-weight: 300;
}
.i-name{
    font-size: 60px;
}
.i-title{
    height: 50px;
    overflow: hidden;

}
.i-title-item{
    height:50px;
    font-size: 30px;
    font-weight: bolder;
    color: rgba(31, 95, 232, 0.812);
    display: flex;
    align-items: center;

}
.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    25%{transform: translateY(-50px);}
    50%{transform: translateY(-100px);}
    75%{transform: translateY(-150px);}
    100%{transform: translateY(-200px);}
   
}

.i-scroll{
 width: 30px;
 height: 30px;
 position: absolute;
 bottom: 20px;   
}

.i-img{
    width: 100%;
    height: 100%;
    object-fit:cover;
    position: absolute;
}
.i-bg{
    clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: rgba(31, 95, 232, 0.812);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}


@media screen and (max-width:480px){
    .i{
        flex-direction: column;
        
    }

    .i-wrapperleft{
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }

    .i-desc{
        display: none;
    }
   
   

}
